.request_card {
  padding: 1.5vw;
  font-family: "Inter";
  font-style: normal;
  width: 100%;
}
.request_card_staff {
  font-family: "Inter";
  font-style: normal;
  width: 100%;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  padding: 1vw;
  margin-bottom: 0.5vw;
}
.request_card_header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.request_card_text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}

.request_card_text > img {
  height: 2.7vw;
  width: 2.7vw;
  border-radius: 6px;
}

.request_card_inner_text > h6 {
  font-weight: 500;
  font-size: 1vw;
  line-height: 17px;
  margin-bottom: 0.4vw !important;
  color: #000000;
}

.request_card_inner_text > p {
  font-style: normal;
  font-weight: 400;
  font-size: 0.9vw;
  line-height: 15px;
  margin-bottom: 0 !important;
  color: rgba(18, 18, 18, 0.7);
}

.btn {
  font-weight: 400;
  font-size: 0.9vw;
  line-height: 15px;
  cursor: pointer;
  /* color: #14ab50; */
  color: rgba(18, 18, 18, 0.7);
}
.request_card_btn_group {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}
.accept_btn,
.reject_btn {
  text-align: center;
  width: 6rem;
  border-radius: 6px;
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  cursor: pointer;
  color: #ffffff;
  padding: 10px 13px;
}
.reject_btn {
  background-color: #6f727c;
}
.accept_btn {
  background: #001a72;
}
.viewAll {
  margin-left: 5px;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #0a66c2;
  cursor: pointer;
}
